.Layout{
    height: 100%;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 15% 80% 5%;
}
.Layout, .Layout > *{
    border: 1px solid black;

}

header, footer{
    grid-column: span 6;

}
main{
    grid-column: span 5;
    overflow: auto;
}